import {
    Component,
    h,
    Host,
    Prop,
    Element,
    Event,
    EventEmitter,
} from '@stencil/core';
import { classlist } from '../../utils/classlist';

export type InputType = 'text' | 'url' | 'email' | 'password';

const inputVariantsConfig = {
    variants: {
        disabled: {
            false: 'focus-within:ring-active-foreground/30 bg-white',
            true: 'bg-surface-1 !text-disabled pointer-events-none border-none',
        },
        readonly: {
            false: 'focus-within:ring-active-foreground/30',
            true: '!bg-surface-1 !text-dark cursor-text border-none',
        },
        invalid: {
            false: 'border-med focus-within:border-active-dark focus-within:ring-active-foreground/30',
            true: 'border-error-base focus-within:border-error-base focus-within:ring-destructive-foreground/30',
        },
    },
    defaultVariants: {
        disabled: false,
        readonly: false,
        invalid: false,
    },
};

/**
 * @category Form Controls
 * @description A comprehensive text input component with label, validation, hints, and accessibility features. Supports multiple input types, clearable functionality, and integrates with form validation.
 */
@Component({
    tag: 'at-input',
    shadow: false,
})
export class AtInputComponent {
    /**
     * Label above the input container
     */
    @Prop()
    label: string;

    /**
     * Type of the input
     */
    @Prop()
    type: InputType = 'text';

    /**
     * Short description or validation hint if required
     */
    @Prop()
    hint_text: string;

    /**
     * Optional info icon with detailed tooltip description
     */
    @Prop()
    info_text: string;

    /**
     * Error text displayed when invalid is set
     */
    @Prop()
    error_text: string;

    /**
     * Placeholder text to be shown when no input is passed
     */
    @Prop()
    placeholder: string | number = '';

    /**
     * Adds a required * to the element
     */
    @Prop()
    required: boolean;

    /**
     * Shows the error text
     */
    @Prop()
    invalid: boolean;

    /**
     * Sets the 'autocomplete' attribute on the input element
     */
    @Prop()
    autocomplete: string = 'on';

    /**
     * Makes the input read only
     */
    @Prop()
    readonly: boolean;

    /**
     * Disables interaction with the input
     */
    @Prop()
    disabled: boolean = false;

    /**
     * Allows the input to be cleared with a 'x' at the end of the input element
     */
    @Prop()
    clearable: boolean;

    /**
     * The value of the input
     */
    @Prop({ mutable: true })
    value: string;

    inputEl: HTMLInputElement;
    @Element() el: HTMLAtInputElement;

    /**
     * Emits an event containing the text string or number when the input's content is changed
     */
    @Event()
    atuiChange: EventEmitter<string | number>;

    private inputId = `input-${Math.random().toString(36).substring(2, 11)}`;

    handleChange(event: Event): void {
        event.stopPropagation();
        this.value = (event.target as HTMLInputElement).value;
        this.atuiChange.emit(this.value);
    }

    handleClear(): void {
        this.inputEl.value = '';
        this.value = '';
        this.atuiChange.emit('');
    }

    render() {
        const getClassname = classlist(
            'placeholder-text-light pr -24 relative flex min-h-36 min-h-[36px] w-full flex-shrink flex-grow basis-0 gap-4 overflow-hidden rounded-md border border-solid overflow-ellipsis transition-colors duration-300 ease-in-out outline-none focus-within:ring-2 focus-within:outline-0',
            inputVariantsConfig,
        );

        const classname = getClassname({
            invalid: this.invalid,
            disabled: this.disabled,
            readonly: this.readonly,
        });

        return (
            <Host>
                <div class="flex flex-col">
                    <slot name="label" />
                    {(this.label || this.required || this.info_text) && (
                        <at-form-label
                            label={this.label}
                            for={this.inputId}
                            required={this.required && !this.readonly}
                            info_text={this.info_text}
                        />
                    )}

                    {this.hint_text && (
                        <span
                            class="text-light mb-8 inline-block text-xs leading-tight"
                            data-name="input-hint"
                        >
                            {this.hint_text}
                        </span>
                    )}
                </div>

                <div class={classname}>
                    <input
                        class="flex w-full py-8 pl-8 outline-0"
                        id={this.inputId}
                        readonly={this.readonly ? true : undefined}
                        disabled={this.disabled ? true : undefined}
                        required={this.required}
                        type={this.type}
                        placeholder={`${this.placeholder}`}
                        autocomplete={this.autocomplete}
                        autocapitalize="none"
                        onInput={(event) => this.handleChange(event)}
                        onChange={(event) => this.handleChange(event)}
                        value={this.value}
                        ref={(el) => (this.inputEl = el)}
                        data-name="input"
                    />

                    <div class="flex items-center py-4 pr-4">
                        <slot name="input-actions" />

                        {this.clearable &&
                            !this.readonly &&
                            !this.disabled &&
                            this.value && (
                                <at-button
                                    class={`transition-all duration-300 ease-in-out ${
                                        this.clearable &&
                                        !this.readonly &&
                                        !this.disabled &&
                                        this.value
                                            ? 'pointer-events-auto scale-100 opacity-100'
                                            : 'pointer-events-none scale-90 opacity-0'
                                    }`}
                                    size="sm"
                                    icon="cancel"
                                    type="secondaryText"
                                    onClick={(event) => {
                                        event.stopPropagation();
                                        this.handleClear();
                                    }}
                                    data-name="input-clear"
                                />
                            )}
                    </div>
                </div>

                {this.error_text && this.invalid && (
                    <span
                        class="text-error text-sm"
                        data-name="input-error"
                    >
                        {this.error_text}
                    </span>
                )}
            </Host>
        );
    }
}
